<template>
    <el-autocomplete v-model="inputContent" placeholder="" :fetch-suggestions="querySearch"
                     suffix-icon="el-icon-search" @select="handleSelect" @blur="clearCityMap" >

    </el-autocomplete>
</template>

<script>
    import {post} from "../../network/post";
    import axios from "axios"

    export default {
        name: "SearchBar",
        data(){
            return{
                inputContent:"",
                state: '',
                timeout:  null,
                searchResult:[],
            }
        },
        methods:{
            /**
             * 
             * @param queryString 
             * @param callback  
             */
            querySearch(queryString, callback){
                //querystring 
                //,
                if(queryString.trim()===""||queryString.trim===null)
                    return callback([]);
                else {
                    //
                    // clearTimeout(this.timeout);
                    // this.timeout = setTimeout(() => {
                    //     callback([]);
                    // }, 3000 * Math.random());
                    post({
                        url:"./SearchServlet",
                        data:{
                            name:queryString
                        },
                        params:{
                            //
                            //TODO payloadform-data
                            name:queryString
                        }
                    }).then((res)=>{
                        if(res.data.status===200){
                            let geojson = JSON.parse(res.data.data.geojson);
                            this.getArrayFromGeoJson(geojson);
                            this.$parent.$parent.$refs["mapview"].createFeatureByGeoJson(res.data.data.geojson);
                            return callback(this.searchResult);
                            //TODO 
                        }else {
                            return callback([]);
                        }
                    });

                }
            },
            /***
             *  Json
             * @param geojson
             */
            getArrayFromGeoJson(geojson){
                this.searchResult = [];
                for(let feature of geojson.features){
                    this.searchResult.push({
                        id:feature.properties.id,
                        //demo  value
                        value:feature.properties.name
                    });
                }
            },
            /**
             *  
             * @param item  value name idfid
             */
            handleSelect(item){
                this.$emit("SearchInfo",item);
            },
            clearCityMap(){
                if(!this.$store.state.showInfo&&this.inputContent===""){
                    console.log("");
                    this.$EventBus.$emit("clearCityFeature")
                }
            }
        }
    }
</script>

<style scoped>
    .el-autocomplete{
        width: 350px;
        height: 30px;
    }
</style>