<template>
    <div class="register_div">
        <el-form :model="ruleForm" label-width="100px" status-icon  :rules="rules" ref="ruleForm">
            <el-form-item label="" prop="username">
                <el-input autocomplete="off" v-model="ruleForm.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input autocomplete="off" type="password" v-model="ruleForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="checkPass">
                <el-input autocomplete="off" type="password" v-model="ruleForm.checkPass"></el-input>
            </el-form-item>
            <el-form-item  class="login-button">
                <el-button type="primary" @click="submitForm('ruleForm')" ></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {post} from "../../network/post";

    export default {
        name: "register_tab",
        data(){
            /***
             * 
             */
            let validateUsername = (rule, value, callback) => {
                if (value === '') {
                    console.log("1.");
                    callback(new Error(''));
                } else {
                    //
                    callback();
                }
            };
            let validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    console.log("2.");
                    if (this.ruleForm.checkPass !== '') {
                        //     ruleformcheckpass
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            let validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.ruleForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return{
                ruleForm:{
                    password:"",
                    checkPass:"",
                    username:"",
                },
                rules: {
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                    username:[
                        { validator: validateUsername, trigger:'blur'}
                    ]
                }
            }
        },
        methods: {
            //
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.register();
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
                console.log("")
            },
            resetForm(formName) {
                this.$refs[formName].resetFields()
            },
            register(){
                //ajax
                post({
                    url:"./RegisterServlet",
                    data:{
                        username:this.ruleForm.username,
                        password:this.ruleForm.password
                    },
                    headers: {'X-Requested-With': 'XMLHttpRequest'},
                }).then((res)=>{
                    this.register_success_event(res.data)
                })
            },
            showError(){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                })
            },
            showSuccess(){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                })
            },
            register_success_event(data){
                //
                if(data.status==200){
                    //
                    this.showSuccess();
                    this.$emit("register_success",data.data)
                }else {
                    this.showError();
                    this.resetForm("ruleForm");
                }
            }

        }
    }
</script>

<style scoped>
    .login-button /deep/ .el-form-item__content{
        margin-left:50px;
    }
    .login-button{
        margin-bottom: 0px;
    }
    /*.el-form-item /deep/ .el-form-item__content{*/
    /*    margin-left: 0px;*/
    /*}*/
    .el-form{
        margin-right: 80px;
        margin-top: 20px;
    }
</style>