<template>
    <div class="register_div">
        <el-form :model="ruleForm" label-width="100px" status-icon  :rules="rules" ref="ruleForm">
            <el-form-item label="" prop="username">
                <el-input autocomplete="off" v-model="ruleForm.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input autocomplete="off" type="password" v-model="ruleForm.password"></el-input>
            </el-form-item>
            <el-form-item class="login-button" >
                <el-button type="primary" @click="submitForm('ruleForm')" ></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {post} from "../../network/post";
    import axios from "axios"
    export default {
        name: "login_in_tab",
        data(){
            /***
             * 
             */
            let validateUsername = (rule, value, callback) => {
                if (value === '') {
                    console.log("1.");
                    callback(new Error(''));
                } else {
                    //
                    callback();
                }
            };
            let validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    console.log("2.");
                    if (this.ruleForm.checkPass !== '') {
                        //     ruleformcheckpass
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            return{
                ruleForm:{
                    password:"",
                    username:"",
                },
                rules: {
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    username:[
                        { validator: validateUsername, trigger:'blur'}
                    ]
                }
            }
        },
        methods: {
            //
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.login(this.showError,this.showSuccess);
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
                console.log("")
            },
            resetForm(formName) {
                this.$refs[formName].resetFields()
            },
            showError(){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                })
            },
            showSuccess(){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                })
            },
            login_success_event(data){
                if(data.status===200){
                    // 
                    this.showSuccess();
                    //1.
                    this.$store.commit("CloseLoginTab");
                    this.$store.commit("login_in");

                    this.$emit("login_success",data.data);

                }
                else {
                    // 
                    this.showError()
                }
            },
            login(){
                // ajax
                post({
                    url:"./LoginServlet",
                    params:{
                        LoginQuery:"2"
                    },
                    data:{
                        username:this.ruleForm.username,
                        password:this.ruleForm.password
                    },
                }).then((res)=>{
                    //
                    this.login_success_event(res.data)
                });
            }
        }
    }
</script>

<style scoped>
    /*.login-button /deep/ .el-form-item__content{*/
    /*    margin-left: 0px;*/
    /*}*/
    .login-button {
        margin-bottom: 0px;
    }
    .el-form{
        margin-right: 80px;
        margin-top: 20px;
    }
</style>